.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.IntroOverlay h1{
	font-size:70px;
}
.IntroOverlay p b{
	font-size:35px;
}
.IntroOverlay{
	color:white;
	position:absolute;
	z-index:1;
	text-size:40px;
	rotate:-10deg;
	padding:30px;
	bottom:20px;
	right:20px;
}